.App {
  max-width: 500px;
  width: 100%;
  min-height: 400px;
  box-shadow: 0 2px 6px;
  margin: 50px auto;
  padding: 50px;
}

ul li{
  list-style: none;
  border: 1px solid #ccc;
  margin: 10px 0;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-transform: capitalize;
}
ul li .active{
  opacity: 0.5;
  text-decoration: line-through;
}
ul li input
.row input{
  margin-right: 5px;
  transform: translateY(1px);
}

ul li button:disabled{
  opacity: 0.5;
  cursor: not-allowed;
}


@media(max-width: 400px){
  .App{
    padding: 10px;
  }
}

